import React from "react";
import { rhythm } from "../utils/typography";
import Helmet from "react-helmet";
import profile from "../images/profile_phamvanlam-com-co-dien.jpg";
import WidgetContainer from "../components/widget-container";
import FacebookProvider, { Comments } from 'react-facebook';

export default ({ data }) => {
  const seoTitle = `Gii thiu - ${data.site.siteMetadata.title}`;
  const url = `${data.site.siteMetadata.siteUrl}/gioi-thieu/`;
  
  return (
    <article>
      <Helmet
        title={seoTitle}
        meta={[
          { name: 'description', content: `${data.site.siteMetadata.description}` },
          { name: 'keywords', content: `${data.site.siteMetadata.keywords}` },
        ]}
      />

      <h1>Gii thiu</h1>
      <WidgetContainer>
        <img
          src={profile}
          alt="profile_phamvanlam-com-co-dien"
          width="252" height="auto"
          style={{ float: `left`, paddingRight: `${rhythm(1 / 2)}` }}
        />
        <p>
          Xin cho cc bn! Ti l Phm Vn Lm, cu sinh vin i hc Bch Khoa H Ni chuyn ngnh in t  Vin thng. Hin ti, ti ang lm vic ti SVMC  trung tm nghin cu v pht trin in thoi di ng Samsung ti Vit Nam.
      </p>
        <p>
          Ti lp nn trang web ny vi mc ch chia s nhng kin thc v kinh nghim ca bn thn v cc thut ton v li gii cc bi ton c bn m ti  thc hnh trn cc trang nh spoj.com, acm.timus.ru,...
      </p>
        <p>
          Rt mong nhn c s ng h v ng gp  kin ca cc bn  trang web ngy cng hon thin hn. Qua , ti c thm nhiu ng lc  pht trin trang web ny.
      </p>
        <p>
          Xin chn thnh cm n!
      </p>
        <p>
          Phm Vn Lm.
      </p>
      </WidgetContainer>

      <WidgetContainer>
        <FacebookProvider appId={data.site.siteMetadata.fbCommentId}>
          <Comments
            href={url}
            width="100%"
            dataNumposts="5"
          />
        </FacebookProvider>
      </WidgetContainer>
    </article>
  )
}

export const query = graphql`
  query AboutQuery {
    site {
      siteMetadata {
        siteUrl
        title
        subtitle
        description
        keywords
        fbCommentId
      }
    }
  }
`


